}
}
/* modal */

.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 50;
    width: 100%;
    height: 100%;
    background-color: #FFFDEE;
    
    transition: opacity var(--transition-func),
               transform var(--transition-func),
               visibility var(--transition-func);
}  
  
  .backdrop.is-hidden{
opacity: 0;
visibility: hidden;
pointer-events: none;
transform: scale(0.3);
 }

 .modal-amn{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 414px;
    background-color: #FFFDEE ;
    border: 1px solid var(--brdr-btn-yel);
    border-radius: 4px;
    padding: 60px 15px;
}

    .close-window-amn{
        position: absolute;
        border: none;
        top: 16px;
        right: 16px; 
    } 
    .modal-title{
        display: block;
    font-family: var(--font-secondary);
        font-weight: 700;
    font-size: 24px;
    line-height: 1.17;
    text-align: center;
    letter-spacing: 0.02em;
    margin-bottom: 40px;
    color: #000;
    }
    .modal-kind{
    margin-bottom: 40px;
}
    .modal-form-amn{ 
        display: flex;
       flex-direction: column;
      }
      .mf-label{
        display: block;
        margin-bottom: 20px;
      }
      .mf-label:last-child{
        margin-bottom: 0;
      }
      

      .mf-input{
        width: 100%;
        height: 54px;
       padding-left: 12px;
        border: 1px solid #C79002;
      border-radius: 2px;
      margin-bottom: 8px;
     
      transition: border-color var(--transition-func);
      }
    .kind-room{
        display: inline-block;
        height: 64px;
    }

      .mf-input::placeholder{
        font-family: var(--font-secondary);
        font-weight: 700;
        font-size: 20px;
        line-height: 1.15;
        letter-spacing: 5%;
        color: #808080;
      }
      .mf-desk{
        font-family: var(--font-primary);
        color: #C79002;
        font-size: 14px;
        line-height: 1.2;
        letter-spacing: 2%;
        padding-left: 12px;
      }
      .mf-button{
        width: 384px;
        border: var(--brdr-btn-yel);
        border-radius: 2px;
        font-family: var(--font-primary); 
       font-weight: 500;
    font-size: 20px;
    line-height: 1.2;
    color: var(--btn-act-white);
    background-color: var(--btn-bg-dark);
    padding: 20px 43px;
    margin: 0 auto;
    outline: none;
    transition: background-color var(--transition),
    color var(--transition);
    } 
    .mf-button:hover,
    .mf-button:focus{
        color: var(--btn-bg-dark);
        background-color: var(--btn-act-yellow);
    }

    .mf-select,
    .selection{
        font-family: var(--font-secondary);
        font-weight: 700;
        font-size: 20px;
        line-height: 1.15;
        letter-spacing: 5%;
        color: #808080;
        
    }
    .mf-input:focus{
        outline: none;
        box-shadow: 0px 4px
        rgba(0, 0, 0, 0.25);
        
    }
    select { 
    
        appearance: none; 
        overflow: hidden;
        background: url('../images/svg/sprite.svg#icon-wedge') no-repeat right;
        background-position-x: calc(100% - 17px); 
      }

      input::-webkit-calendar-picker-indicator {
        opacity: 0;
    }
      .kalendar{
        appearance: none; 
        overflow: hidden;
        background: url('../images/svg/sprite.svg#icon-wedge') no-repeat right;
        background-position-x: calc(100% - 17px)
      }
      @media screen and (min-width: 1024px){
        .modal-amn{
            width: 712px;
            padding: 60px 60px;
        }
        .modal-title{
        font-size: 40px;
        line-height: 1.15;
        letter-spacing: 5%;
        margin-bottom: 36px;
      }

        .modal-kind{
          display: flex;
         flex-wrap: wrap;
         justify-content: flex-end;
         
        }
        .mf-input{
            width: 288px;
            height: 64px;
        }
        .use-info{
            display: flex;
            margin-bottom: 28px;
        }
        .use-info:last-child{
            margin-bottom: 0;
        }
        .mf-label:nth-child(3){
            margin-bottom: 8px;
      } 
      .mf-label:nth-child(2n){
        margin-left: 16px;
      }
      .mf-label:nth-child(-n+2){
        margin-bottom: 0;
      }
      .use-room{
        display: flex;
        flex-direction: column;
        margin-bottom: 28px; 
    }
    .mf-button{
        margin-top: 0;
    }
    }
    